$mobile: 415px;
$tablet: 820px;


@mixin mobile{
  @media (max-width: $mobile){
    @content;
  }
}


@mixin tablet{
  @media (max-width: $tablet){
    @content;
  }
}


/*넓이, 높이 자동 계산함수*/
@mixin square($size){
  $calculated: 32px * $size;
  width: $calculated;
  height: $calculated;
}