@import "../utils.scss";

.login {
  @include tablet {
    .login {
      &.__pic {
        background: url("../components/images/mobile.png");
        background-repeat: no-repeat;
        background-position: center center;
        background-size: contain;
        top: -15%;
        width: 50%;
      }
      &.__text {
        display: none;
      }
    }
    .login__formOpen {
      width: 60vw;
      height: 45vw;
      .login__wrap {
        .closeForm {
          font-size: 4vmax;
        }
        .signIn {
          font-size: 4vmax;
        }
        input {
          font-size: 4vmax;
          &::placeholder {
            font-size: 3vmax;
          }
        }
      }
    }
    .login__form {
      top: 70%;
      right: 50%;
      transform: translate(50%, -50%);
      width: 40%;
      font-size: 4vmax;
    }
  }
  @include mobile {
    .slideTexts {
      top: 40%;
    }
    .login.__pic {
      top: -10%;
      width: 70%;
    }
    .login__form {
      top: 80%;
      font-size: 3vmax;
    }
    .login__formOpen {
      width: 60vw;
      height: 55vw;
      .login__wrap {
        .closeForm {
          font-size: 3vmax;
        }
        .signIn {
          font-size: 3vmax;
        }
        input {
          font-size: 2.5vmax;
          &::placeholder {
            font-size: 2vmax;
          }
        }
      }
    }
  }
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  position: relative;
  &.__pic {
    z-index: 5;
    position: absolute;
    height: 100%;
    width: 33%;
    background: url("../components/images/main.png");
    background-repeat: no-repeat;
    background-position: center center;
    background-size: contain;
  }
  &.__text {
    position: absolute;
    top: 40%;
    width: 100%;
    height: 30%;
    display: grid;
    align-items: center;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: 1fr;
    div {
      z-index: 6;
      line-height: 1.2;
      text-align: center;
      grid-column: 1/2;
      font-size: 5.5vmax;
      font-style: italic;
      font-weight: 500;
      justify-self: end;
      margin-right: -20%;
    }
    h1 {
      grid-column: 3/4;
      margin-left: -7%;
      font-style: italic;
      font-weight: 800;
      font-size: 8.5vmax;
    }
  }
  .slideTexts {
    @include mobile {
      top: 40%;
    }
    position: absolute;
    top: 30%;
    width: 100vw;
    height: 12%;
    overflow: hidden;
    .slideTexts__wrap {
      display: flex;
      height: 100%;
      span {
        display: inline-block;
        color: #ff9e2d;
        font-size: 5vmax;
        font-weight: 500;
        -webkit-text-stroke: 1px black;
        margin-right: 2%;
        &:nth-child(odd) {
          color: #fbff23;
        }
      }
    }
  }
}

.login__formOpen {
  z-index: 100;
  position: absolute;
  width: 30vw;
  height: 20vw;
  .login__wrap {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    width: 100%;
    height: 100%;
    border: 2px solid black;
    background-color: #fff;
    box-shadow: 15px 15px 0px #94ff6f;
    .closeForm {
      cursor: pointer;
      font-size: 2vmax;
      position: absolute;
      right: 3%;
      top: 3%;
    }
    .signIn {
      text-transform: uppercase;
      -webkit-text-stroke: 1px black;
      color: #fff;
      font-size: 2.5vmax;
      line-height: 2;
      width: 100%;
      text-align: center;
    }
    input {
      margin: 0 1%;
      z-index: 50;
      text-align: center;
      cursor: pointer;
      font-size: 2vmax;
      border-radius: 50px;
      height: 15%;
      width: 80%;
      &:hover,
      :focus {
        &::placeholder {
          opacity: 0;
        }
      }
      &:valid + .submit {
        color: #258eef;
      }
      &::placeholder {
        transition: 0.3s ease-in-out;
        font-size: 1.5vmax;
        text-transform: uppercase;
      }
    }
    .submit {
      text-align: center;
      font-weight: 500;
      -webkit-text-stroke: 1px black;
      color: #fff;
      z-index: 100;
      cursor: pointer;
      transition: 0.3s linear;
      padding: 0;
      text-transform: uppercase;
      background: none;
      margin-bottom: 5%;
    }
  }
}
.login__form {
  text-transform: uppercase;
  z-index: 100;
  position: absolute;
  top: 2%;
  right: 2%;
  width: 8%;
  height: 5%;
  font-size: 1.2vmax;
  .login__wrap {
    display: flex;
    justify-content: center;
    width: 100%;
    border: 2px solid black;
    border-radius: 50px;
    background-color: #fff;
    .signIn {
      line-height: 2;
      cursor: pointer;
      width: 100%;
      text-align: center;
    }
    .submit,
    input,
    .closeForm {
      display: none;
    }
  }
}
