@import "../utils.scss";

nav {
  @include tablet {
    height: 5vw;
  }
  position: fixed;
  width: 100vw;
  height: 2vw;
  z-index: 300;
  .link {
    top: 50%;
    position: absolute;
    height: 300%;
    width: 10%;
    div {
      margin-left: 10%;
      width: 100%;
      height: 100%;
      background: url("./images/logo.png") no-repeat;
      background-size: contain;
    }
  }
  .fav {
    @include tablet {
      font-size: 2vmax;
    }
    @include mobile {
      font-size: 1.5vmax;
    }
    background-color: #fff;
    cursor: pointer;
    border-radius: 50px;
    border: 2px solid black;
    position: absolute;
    top: 50%;
    right: 2%;
    font-size: 1.2vmax;
    padding: 0.5% 1%;
  }
}
